<template>
	<swiper class="home-swiper" :current="activeIndex" @change="change" disable-touch>
		<swiper-item v-for="(item ,index) in tab" :key="item._id" class="swiper-item">
			<list-scroll class="list-scroll" @loadmore="loadmore" v-if="listCatchData[index]!=undefined">
				<love-card :item="item" v-for="(item,index1) in listCatchData[index]" :key="item._id">{{index1+1}}．</love-card>
				<uni-load-more  iconType="snow" :status="load[index].loading" v-if="listCatchData[index].length>10" class="loadmore"></uni-load-more>
			</list-scroll>
		</swiper-item>
	
	</swiper>
	
</template>

<script>
	export default {
		props: {
			tab: {
				type: Array,
				default () {
					return []
				}
			},
			activeIndex: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				list: [],
				// js 的限制 listCatchData[index] = data
				listCatchData: {},
				load: {loading: "loading"},

				pageSize: 20
			};
		},
		watch: {
			tab(newVal) {
				if (newVal.length === 0) return
				this.listCatchData = {}
				this.load = {}
				this.getList(this.activeIndex)
			}
		},
		// onLoad 在页面 ，created 组件
		created() {
			// TODO tab 还没有赋值
			this.getList(0)
			let _this = this
			uni.$on('reload',function(data){
				if(data){
					_this.listCatchData = {}
					_this.getList(_this.activeIndex)
					_this.$forceUpdate()
				}
			})
			
		},
		methods: {
			loadmore() {
				if (this.load[this.activeIndex].loading === 'noMore') return
				this.load[this.activeIndex].page++
				this.getList(this.activeIndex)
			},
			change(e) {
				const {
					current
				} = e.detail
				this.$emit('change', current)
				// TODO 当数据不存在 或者 长度是 0 的情况下，才去请求数据
				if (!this.listCatchData[current] || this.listCatchData[current].length === 0) {
					this.getList(current)
				}

			},
			getList(current) {
				if (!this.load[current]) {
					this.load[current] = {
						page: 1,
						loading: 'loading'
					}
				}
				// console.log('当前的页数',this.load[current].page);
				// console.log('name',this.tab[current]);
				this.$api.get_love({
					name: this.tab[current],
					page: this.load[current].page,
					pageSize: this.pageSize
				}).then(res => {
					// console.log(res);
					const {
						data
					} = res
					if (data.length === 0) {
						let oldLoad = {}
						oldLoad.loading = 'noMore'
						oldLoad.page = this.load[current].page
						this.$set(this.load, current, oldLoad)
						// 强制渲染页面
						this.$forceUpdate()
						return
					}
					let oldList = this.listCatchData[current] || []
					oldList.push(...data)
					oldList.sort(function(a,b){
						return b.date < a.date ? 1 : -1
					})
					// 懒加载
					this.$set(this.listCatchData, current, oldList)
				})
			this.$forceUpdate()	
			}
		}
	}
</script>

<style lang="scss">
	.loadmore{
		padding-bottom: 5px;
	}
	.home-swiper {
		margin: 5px 0;
		height: 100%;
		.swiper-item {
			height: 100%;
			overflow: hidden;

			.list-scroll {
				height: 100%;
			}
		}
	}
</style>
